import * as vscode from "vscode";
import { ExtensionContext } from "vscode";
import {
    LogLevel,
    Settings,
    TelemetryEventNames,
    LanguageId,
    ILoggingService,
} from "@vsintellicode/completions-shared";
import { CompletionState } from "@vsintellicode/completions-shared/resultCache";
import {
    SettingsStorage,
    initNormalizationService,
} from "@vsintellicode/completions-shared/services";
import { initWorkerCompletionService } from "@vsintellicode/completions-worker-wrapper";
import { FaultType } from "@vsintellicode/pythia-compose-client";
import {
    IntelliCodeInlineCompletionItemProvider,
    TreeSitterSyntacticFilterService,
    initVsCodeTelemetryService,
    treatmentVariablesType,
    postAcceptanceCommand,
} from "@vsintellicode/pythia-compose-client"; 

export async function getIntelliCodeInlineCompletionProvider(context: ExtensionContext, settings: Settings, settingsStorage: SettingsStorage, logger: ILoggingService) : Promise<vscode.InlineCompletionItemProvider | null> {
    const telemetry = initVsCodeTelemetryService(context, logger);
    context.subscriptions.push(postAcceptanceCommand(telemetry));

    try {
        const normalizationService = await initNormalizationService(logger);
        const syntacticFilterService = new TreeSitterSyntacticFilterService(logger);
        const completionState = new CompletionState(logger, telemetry);
        const lineCompletionService = await initWorkerCompletionService(settingsStorage, telemetry, logger);

        const intelliCodeInlineCompletionItemProvider: vscode.InlineCompletionItemProvider =
            new IntelliCodeInlineCompletionItemProvider(
                normalizationService,
                syntacticFilterService,
                logger,
                telemetry,
                completionState,
                lineCompletionService,
                settings,
                context,
                undefined,
                getTreatmentVariables(settings),
            );

            telemetry.sendTelemetryEvent(TelemetryEventNames.EXTENSION_ACTIVATED);
        return intelliCodeInlineCompletionItemProvider;
    }
    catch(e){
        if (!(e instanceof Error)) {
            logger.write(LogLevel.ERROR, `IntelliCode Completions could not be enabled. ${e}`);
            throw e;
        }
        telemetry.sendFault(
            TelemetryEventNames.EXTENSION_ACTIVATION_FAULT,
            FaultType.Unknown,
            e.message,
            e,
            undefined
          );
        logger.write(LogLevel.ERROR, `IntelliCode Completions could not be enabled. ${e}:${e.stack}`);
        throw e;
    }
}

function getTreatmentVariables(settings: Settings): Map<string, treatmentVariablesType>{
    const result = new Map<string, treatmentVariablesType>();
    result.set(LanguageId.JAVASCRIPT, createTreatmentVariable(LanguageId.JAVASCRIPT, settings));
    result.set(LanguageId.TYPESCRIPT, createTreatmentVariable(LanguageId.TYPESCRIPT, settings));
    result.set(LanguageId.JAVASCRIPT_REACT, createTreatmentVariable(LanguageId.JAVASCRIPT_REACT, settings));
    result.set(LanguageId.TYPESCRIPT_REACT, createTreatmentVariable(LanguageId.TYPESCRIPT_REACT, settings));
    result.set(LanguageId.PYTHON, createTreatmentVariable(LanguageId.PYTHON, settings));
    return result;
}

function createTreatmentVariable(language: string, settings: Settings){
    return {
        "confidenceThreshold": settings.getLogConfidence(language),
        "expTriggeringCharacters": false
    };
}
