import * as vscode from 'vscode';
import { getIntelliCodeInlineCompletionProvider } from './intellicodeInlineCompletionProvider';
import { VSCodeOutputLogger, VSCodeSettings } from '@vsintellicode/pythia-compose-client';
import { LogLevel, Settings } from '@vsintellicode/completions-shared';

// These constants are populated at build time by webpack, and are defined in webpack.config.js.
declare let __PACKAGE_VERSION__: string;
declare let __COMMIT_HASH__: string;

export let logger: VSCodeOutputLogger | undefined;

// Languages currently support in WLC
const documentSelector = [
	"javascript",
	"javascriptreact",
	"typescript",
	"typescriptreact",
	"python",
  ];

const ROOT_SETTING_SECTION = "intellicodeCompletions";

export async function activate(context: vscode.ExtensionContext) {
	const settingsStorage = new VSCodeSettings(ROOT_SETTING_SECTION);
	const settings = new Settings(settingsStorage);
	logger = new VSCodeOutputLogger(context, context.extension.packageJSON.displayName, settings.RootSectionName);

	logger.write(
		LogLevel.INFO,
		`Extension version: ${__PACKAGE_VERSION__} ${__COMMIT_HASH__}`
	);

	const icp = await getIntelliCodeInlineCompletionProvider(
		context,
		settings,
		settingsStorage,
		logger
	);

	if (icp === null) {
		return;
	}

	context.subscriptions.push(vscode.languages.registerInlineCompletionItemProvider(
		documentSelector,
		icp
	));
}
